<template>
<div>
	<ul>
		<li>{{city}}</li>
	</ul>
	<Cell title="" arrow>
		<Selector slot="body" v-model="city" placeholder="">
			<option :value="1"></option>
			<option :value="2"></option>
			<option value="3"></option>
			<option value="4"></option>
		</Selector>
	</Cell>
</div>	
</template>

<script>
export default {
	data() {
		return {
			city: ""
		}
	}
}
</script>


